module.exports = "<h1 id=\"what-and-how-to-use-them-\">What and How to Use Them?</h1>\n<h2 id=\"webpack\">webpack</h2>\n<blockquote>\n<p>模块打包工具</p>\n</blockquote>\n<h3 id=\"cli-\">cli配置参数</h3>\n<ul>\n<li>--config 使用配置文件名字</li>\n<li>--watch 监听代码变化</li>\n<li>--colors 有颜色区分</li>\n<li>--optimize-minimize 压缩代码</li>\n</ul>\n<h2 id=\"config-\">config参数</h2>\n<ul>\n<li>.entry 入口文件可以多入口或者单入口{}/PathString</li>\n<li>.output<ol>\n<li>path 路径</li>\n<li>publicPath 发布后url的cdn地址<a href=\"https://segmentfault.com/q/1010000004674968\">参考</a></li>\n<li>filename [name].js  输出文件名字跟入口保持一致的写法。否则写死</li>\n</ol>\n</li>\n<li><a href=\"http://www.cnblogs.com/hhhyaaon/p/5657469.html\">.devtool</a></li>\n<li>.module.loaders(list){test:/.jade$/,loader:&#39;jade&#39;,exclude: /(node_modules)/}<ol>\n<li>test 正则匹配文件后缀</li>\n<li>loader 插件名字</li>\n<li>exclude 排除文件</li>\n</ol>\n</li>\n<li>.plugins(list) 插件</li>\n<li>.resolve.alias(json) 别名</li>\n<li>.resolve.extensions(list) 自动配对后缀名</li>\n<li>.externals(json) 全局变量\n{\njquery: &#39;window.jQuery&#39;,\nbackbone: &#39;window.Backbone&#39;,\nunderscore: &#39;window._&#39;\n}</li>\n</ul>\n<h3 id=\"-optimize-http-webpack-github-io-docs-list-of-plugins-html-\"><a href=\"http://webpack.github.io/docs/list-of-plugins.html\">optimize插件项</a></h3>\n<ul>\n<li>webpack.optimize.CommonsChunkPlugin(options)//分离公共项</li>\n</ul>\n<h3 id=\"webpack-loader\">webpack loader</h3>\n<ul>\n<li>css-loader</li>\n<li>sass-loader</li>\n<li>url-loader 转换成base64</li>\n</ul>\n<h3 id=\"plugins\">plugins</h3>\n<ul>\n<li>extract-text-webpack-plugin 分离文件通常css<pre><code>var ExtractTextPlugin = require(&#39;extract-text-webpack-plugin&#39;);\nvar extractSASS = new ExtractTextPlugin(&#39;[name]-[hash].css&#39;);\nplugins.push(extractSASS);\n//loader\n{\n    test: /\\.scss$/i,\n    loader: extractSASS.extract([&#39;css&#39;,&#39;sass&#39;])\n},\n</code></pre></li>\n<li>html-webpack-plugin 处理html new HtmlWebpackPlugin(conf)<pre><code>var conf = {\n    filename: chunkname+&#39;.html&#39;,\n    template: pages[chunkname],\n    inject: true,\n    minify: {\n        removeComments: true,\n        collapseWhitespace: false\n    },\n    chunks: [&#39;common&#39;,chunkname],\n    hash: false,\n    complieConfig: compileConfig\n  }\nvar titleC = compileConfig.title || {};\nvar title = titleC[chunkname];\nif (title) {\n    conf.title = title;\n}\nplugins.push(new HtmlWebpackPlugin(conf));\n</code></pre></li>\n</ul>\n<h2 id=\"browser-sync\">browser-sync</h2>\n<pre><code>\n</code></pre><h2 id=\"-glob-https-github-com-isaacs-node-glob-\"><a href=\"https://github.com/isaacs/node-glob\">glob</a></h2>\n<blockquote>\n<p>获取文件</p>\n</blockquote>\n";

