<template>
  <div>
    <el-table
      :data="tableData"
      border
      style="width: 100%">
      <el-table-column
        prop="modifyTime"
        label="最后修改日期"
        sortable
        width="180">
        <template scope="scope">
          <span>{{scope.row.modifyTime|formatter}}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="title"
        label="标题"
        width="">
        <template scope="scope">
          <router-link :to="{name:'article',params: { articleId:scope.row.fileCode}}"  >{{ scope.row.title }}</router-link>
        </template>
      </el-table-column>
      <el-table-column
        prop="fileCode"
        label="标签"
        width="100"
        :filters="[{ text: '家', value: '家' }, { text: '公司', value: '公司' }]"
        :filter-method="filterTag"
        filter-placement="bottom-end">
      </el-table-column>
    </el-table>
  </div>
</template>
<style>

</style>
<script>
  import articles from '../../markdown/articles';
  export default {
    data() {
      return {
        tableData: articles.data,
        aaa:222
      }
    },
    filters:{
      formatter : function (value) {
      	var date = new Date(value);
        return date.getFullYear()+'-'+(date.getMonth()+1)+'-'+date.getDate()+' '+date.getHours()+':'+date.getMinutes();
      }
    },
    methods: {
      formatter(row, column) {
        return row.html;
      },
      filterTag(value, row) {
        return row.tag === value;
      }
    }
  }
</script>



