<template>
  <div class="article-container">
    <div class="article-title-info">
      <h1>{{articleBody.title}}</h1>
    </div>
    <p>文章最后修改时间:<strong>{{articleBody.modifyTime}}</strong></p>
    <div class="markdown-body" v-html="articleBody.html" style="padding:10px">
    </div>
  </div>
</template>

<script>
  import articlesJson from '../../markdown/articles';
  const articles = articlesJson.data;
  export default {
    name: 'hello',
    data () {
      return {
        msg: 'asdasd'
      }
    },
    computed:{
    	fileCode:function () {
        if(!this.$route.params.articleId)return null;
        return this.$route.params.articleId.split('-')[1]
      },
      collectionCode : function () {
        if(!this.$route.params.articleId)return null;
        return this.$route.params.articleId.split('-')[0]
      },
      articleBody : function () {
        for (var i =0 ; i < articles.length ; i++){
        	console.log(articles[i].fileCode , this.$route.params.articleId);
          if(articles[i].fileCode == this.$route.params.articleId){
            return  articles[i];
          }
        }
      },
    },
    created(){

    }
  }
</script>

<style lang="scss">

</style>



